@import "../vars";
.display1{
  font-size: 40px;
  line-height: 120%;
  font-weight: 400;
}

.display2{
  font-size: 32px;
  line-height: 150%;
}

.display3{
  font-size: 24px;
  line-height: 150%;
}

.body-text{
  font-size: 16px;
  line-height: 150%;
  p{
    line-height: inherit;
  }
}

p{
  &:not(:last-child){
    margin-bottom: 15px;
  }
}

.bold{
  font-weight: 700;
}

.mlist{
  li{
    line-height: inherit;
    padding-left: 30px;
    position: relative;
    &::before{
      content: '';
      position: absolute;
      top: 11px;
      left: 0;
      width: 14px;
      height: 1px;
      background-color: $primary;
    }
    &:not(:last-child){
      margin-bottom: 13px;
    }
  }
}

@media screen and (max-width: 992px){
  .display1{
    font-size: 26px;
    line-height: 123%;
  }
  .display2,
  .display3{
    font-size: 20px;
    line-height: 166.6%;
  }
  .body-text{
    font-size: 14px;
    line-height: 161%;
  }
  .mlist{
    li{
      padding-left: 25px;
    }
  }
}
